<template>
  <el-table :data="servers" style="width: 100%">
    <el-table-column label="IPv4" align="center" min-width="70px">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.ipv4" type="success">On</el-tag>
          <el-tag v-else type="danger">Off</el-tag>
      </template>
    </el-table-column>
    <el-table-column label="IPv6" align="center" min-width="70px">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.ipv6" type="success">On</el-tag>
          <el-tag v-else type="danger">Off</el-tag>
      </template>
    </el-table-column>
    <el-table-column label="节点名" prop="name" align="center" min-width="110px"></el-table-column>
    <el-table-column label="虚拟化" prop="type" align="center" min-width="110px"></el-table-column>
    <el-table-column label="位置" prop="location" align="center" min-width="120px"></el-table-column>
    <el-table-column label="在线时间" prop="uptime" align="center" min-width="100px"></el-table-column>
    <el-table-column label="负载" prop="load" align="center" min-width="100px"></el-table-column>
    <el-table-column label="网络↓|↑" align="center" min-width="150px">
      <template slot-scope="scope">
          {{ scope.row.network_rx }} | {{ scope.row.network_tx }}
      </template>
    </el-table-column>
    <el-table-column label="流量↓|↑" align="center" min-width="150px">
        <template slot-scope="scope">
          {{ scope.row.network_in }} | {{ scope.row.network_out }}
      </template>
    </el-table-column>
    <el-table-column label="处理器" align="center" min-width="150px">
        <template slot-scope="scope">
            <el-progress v-if="scope.row.cpu >= 80" status="exception" :text-inside="true" :stroke-width="18" :percentage=scope.row.cpu></el-progress>
            <el-progress v-else-if="scope.row.cpu >= 50" color="#FFA500" :text-inside="true" :stroke-width="18" :percentage=scope.row.cpu></el-progress>
            <el-progress v-else status="success" :text-inside="true" :stroke-width="18" :percentage=scope.row.cpu></el-progress>
        </template>
    </el-table-column>
    <el-table-column label="内存" align="center" min-width="150px">
        <template slot-scope="scope">
            <el-progress v-if="scope.row.memory >= 80" status="exception" :text-inside="true" :stroke-width="18" :percentage=scope.row.memory></el-progress>
            <el-progress v-else-if="scope.row.memory >= 50" color="#FFA500" :text-inside="true" :stroke-width="18" :percentage=scope.row.memory></el-progress>
            <el-progress v-else status="success" :text-inside="true" :stroke-width="18" :percentage=scope.row.memory></el-progress>
        </template>
    </el-table-column>
    <el-table-column label="硬盘" align="center" min-width="150px">
        <template slot-scope="scope">
            <el-progress v-if="scope.row.hdd >= 80" status="exception" :text-inside="true" :stroke-width="18" :percentage=scope.row.hdd></el-progress>
            <el-progress v-else-if="scope.row.hdd >= 50" color="#FFA500" :text-inside="true" :stroke-width="18" :percentage=scope.row.hdd></el-progress>
            <el-progress v-else status="success" :text-inside="true" :stroke-width="18" :percentage=scope.row.hdd></el-progress>
        </template>
    </el-table-column>
  </el-table>
</template>

<script>
import 'whatwg-fetch';
export default {
    data() {
        return{
            servers:[],
            expands:[],
            get_row_keys(row) {
                return row.id;
            }
        }
    },
    created: function() {
        var vm = this;
        vm.get_server_status();
        setInterval(() => {
            vm.get_server_status();
        },1000);

    },
    methods: {
        bytes_to_size(bytes, precision, si){
            var ret;
            si = typeof si !== 'undefined' ? si : 0;
            var kilobyte = 1024;
            var megabyte = 1024 * 1024;
            var gigabyte = 1024 * 1024 * 1024;
            var terabyte = 1024 * 1024 * 1024 * 1024;
            if(si !== 0) {
                kilobyte = 1000;
                megabyte = 1000 * 1000;
                gigabyte = 1000 * 1000 * 1000;
                terabyte = 1000 * 1000 * 1000 * 1000;
            }
            if ((bytes >= 0) && (bytes < kilobyte)) {
                ret = bytes + 'B';
            } else if ((bytes >= kilobyte) && (bytes < megabyte)) {
                ret = (bytes / kilobyte).toFixed(bytes/kilobyte > 100 ? 1:precision) + 'K';
            } else if ((bytes >= megabyte) && (bytes < gigabyte)) {
                ret = (bytes / megabyte).toFixed(bytes/megabyte > 100 ? 1:precision) + 'M';
            } else if ((bytes >= gigabyte) && (bytes < terabyte)) {
                ret = (bytes / gigabyte).toFixed(bytes/gigabyte > 100 ? 1:precision) + 'G';
            } else if (bytes >= terabyte) {
                ret = (bytes / terabyte).toFixed(bytes/terabyte > 100 ? 1:precision) + 'T';
            }
            return ret;
        },
        get_server_status(){
            var vm = this;
            fetch('/json/stats.json')
                .then(function(response){
                    return response.json();
                }).then(function(response){
                    var result = [];
                    response.servers.forEach(item => {
                        if(!item.online4 && !item.online6){
                            var tmp = {
                                ipv4        : item.online4,
                                ipv6        : item.online6,
                                name        : item.name,
                                type        : item.type,
                                location    : item.location,
                                uptime      : '-',
                                load        : '-',
                                network_rx  : undefined,
                                network_tx  : undefined,
                                network_in  : undefined,
                                network_out : undefined,
                                cpu         : 100,
                                memory      : 100,
                                memory_used : 0,
                                memory_total: 0,
                                hdd         : 100,
                                hdd_used    : 0,
                                hdd_total   : 0
                            }
                        }else{
                            var tmp = {
                                ipv4        : item.online4,
                                ipv6        : item.online6,
                                name        : item.name,
                                type        : item.type,
                                location    : item.location,
                                uptime      : item.uptime,
                                load        : typeof(item.load) !=='undefined' ? item.load.toFixed(2) : undefined,
                                network_rx  : vm.bytes_to_size(item.network_rx, 2, 1),
                                network_tx  : vm.bytes_to_size(item.network_tx, 2, 1),
                                network_in  : vm.bytes_to_size(item.network_in, 2),
                                network_out : vm.bytes_to_size(item.network_out, 2),
                                cpu         : item.cpu,
                                memory      : Math.round((item.memory_used / item.memory_total) * 100),
                                memory_used : vm.bytes_to_size(item.memory_used, 2),
                                memory_total: vm.bytes_to_size(item.memory_total, 2),
                                hdd         : Math.round((item.hdd_used / item.hdd_total) * 100),
                                hdd_used    : vm.bytes_to_size(item.hdd_used, 2),
                                hdd_total   : vm.bytes_to_size(item.hdd_total)
                            }
                        }
                        result.push(tmp);
                    });
                    vm.servers = result;
                }).catch(function(ex){
                    console.log(ex);
                });
        },
        toggle_row_expansion(row){
            console.log(row.id);
            this.expands.push(row.id);
        }
    },
    computed: {
    }
};

</script>



