import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Index from './views/Index.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '主页',
        iconCls: 'fa fa-home',
        leaf: true,
        redirect: '/index',
        children: [
            {path: '/index', component: Index, name: '状态'}
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
    
];

export default routes;


